const {abi} = require('@predmark/dapps.contracts/src/contracts/abi/GameToken.json'),
    clientContract = require('./clientContracts');

class DelphiERC20 extends clientContract {
    constructor(options) {
        super({
            abi,
            contractAddress: options.network.gameToken,
            network: options.network,
            BICONOMY_API_KEY: options.network.biconomy_api_key,
            instrument: options.instrument
        });
    }

    async getBalance(account) {
        console.log(this);
        return await this.signerContract.balanceOf(account)
     }
}

module.exports = DelphiERC20;