<template>
    <v-row>
        <v-col cols="12" class="text-left">
            <div v-if="!isConnected">
                <h1>Win Gifts Playing Games with your Friends</h1>
                <div class="subtitle">Get started by connecting your wallet</div><br/>
                <v-btn color="secondary" @click="connect()" :disabled="loading">Connect Wallet</v-btn>
            </div>
            <div v-if="isConnected" class="position-relative heading-title">
                <h1 >Play Games with Friends, and earn Delphi Coins</h1>
                <v-row class="pt-2" align="center">
                    <v-col cols="12" md="6">
                        <v-btn color="secondary" :disabled="loading">Refer &amp; Earn</v-btn>
                    </v-col>
                    <v-col cols="12" md="6" class="justify-lg-end d-inline-flex separator">
                        <div>
                            <h4 class="text-left">{{balance}}</h4>
                            <h4>Dephi Coins</h4>
                        </div>
                        <div>
                            <h4 class="text-left">20</h4>
