                    </v-col>
                </v-row>
            </div>
        </v-col>
        <v-col cols="12" class="text-left">
            <h1>Prediction Games</h1>
            <swiper
                class="swiper"
                :options="swiperOptions"
                ref="swiperSlider"
            >
                <swiper-slide class="pt-2 pb-2 cursor-pointer" v-for="(item, idx) in predictionGames" :key="idx">
                    <v-card @click="goToPath(item)">
                        <v-row align="center">
                            <v-col cols="4">
                                <img :src="$vuetify.breakpoint.mdanddown ? item.image.mobile : item.image.desktop" class="img-fluid" />
                            </v-col>
                            <v-col cols="8">
                                <h3>{{item.title}}</h3>
                                <div class="subtitle">{{item.description}}</div>
                            </v-col>
                        </v-row>
                    </v-card>
                </swiper-slide>
            </swiper>
        </v-col>
        <v-col class="text-left">
            <h1>Coming Soon!</h1>
            <v-row>
                <v-col cols="12" md="4" v-for="(item, idx) in comingSoon" :key="idx">
                    <v-card class="bordered" @click="goToPath(item)">
                        <v-row align="center">
                            <v-col cols="4">
                                <img :src="$vuetify.breakpoint.mdanddown ? item.image.mobile : item.image.desktop" class="img-fluid" />
                            </v-col>
                            <v-col cols="8">
                                <h3>{{item.title}}</h3>
                                <div class="subtitle">{{item.description}}</div>
                            </v-col>
                        </v-row>
                    </v-card>
                </v-col>
            </v-row>
        </v-col>
    </v-row>
</template>
