<script>
import { mapGetters } from 'vuex'
import {EventBus} from '../store/eventBus';
import GameTokenContract from "../lib/contracts/delphiERC20"
import placeHolderImage from "../assets/placeholder.png"

export default {
    name: "Home",
    data: () => {
        return {
            balance: 0,
            gameTokenContact: null,
            swiperOptions: {
                slidesPerView: 3,
                spaceBetween: 50,
                slideActiveClass: "dummySlideClass",
                breakpoints: {
                1024: {
                    slidesPerView: 3,
                    spaceBetween: 40
                },
                768: {
                    slidesPerView: 2,
                    spaceBetween: 30
                },
                640: {
                    slidesPerView: 2,
                    spaceBetween: 20
                },
                320: {
                    slidesPerView: 1,
                    spaceBetween: 20
                }
                },
                freeMode: true,

            },
            predictionGames: [{
                title: "Tradable Opinions",
                description: "Buy/Sell Odds of Real life Events, based on your Opinion, Skills and Knowledge.",
                image: {
                    desktop: placeHolderImage,
                    mobile: placeHolderImage
                },
                to: "/tradable-odds",
            },{
                title: "Bull's Eye",
                description: "Make Numerical predictions on the outcomes of Real life Events.",
                image: {
                    desktop: placeHolderImage,
                    mobile: placeHolderImage
                },
                to: "/bulls-eye",
            }
            ],
            comingSoon: [{
                title: "Music Quiz",
                description: "The most unique Daily Music Quiz that hums and jogs your musical memory. Earn Delphis, while you enjoy the music games. Redeem for cool gifts",
                image: {
                    desktop: placeHolderImage,
                    mobile: placeHolderImage
                },
                to: "/quiz"
            },{
                title: "Travel Game",
                description: "The most unique Daily Travel Quiz, that takes you places. Earn Delphis and Redeem for cool discounts and other travel gifts",
                image: {
                    desktop: placeHolderImage,
                    mobile: placeHolderImage
                }
            }]
        }
    },
    computed: {
        ...mapGetters([
            "isConnected",
            "network",
            "account",
            "loading",
            "getSelectedInstrument"
        ])
    },
    watch: {
        isConnected: function(newValue) {
            if(newValue) {
                this.init();
            }
        }
    },
    methods: {
        connect: function() {
            EventBus.$emit("connect")
        },
        init: async function() {
            this.gameTokenContact = new GameTokenContract({
                network: this.network,
                instrument: this.getSelectedInstrument
            })
            await this.gameTokenContact.init();
            this.balance = await this.gameTokenContact.getBalance(this.account)
        },
        goToPath: function(item) {
            this.$router.push(item.to);
        }
    },
    mounted(){
        if(this.isConnected) {
            this.init();
        }
    }
}
</script>
